import { notFound } from "next/navigation";
import { getTreatmentById } from "../../../src/lib/data/treatments";
import TreatmentDetailClient from "./TreatmentDetailClient";

export async function generateMetadata({ params }) {
  const { id } = await params;
  const treatment = id ? await getTreatmentById(id) : null;
  if (!treatment) return {};
  return {
    title: treatment.title,
    description:
      treatment.desc ||
      treatment.fullDescription ||
      "Explore this HEALO treatment in Korea.",
  };
}

export default async function TreatmentDetailPage({ params }) {
  const { id } = await params;
  const treatment = id ? await getTreatmentById(id) : null;
  if (!treatment) notFound();

  return <TreatmentDetailClient id={id} />;
}
